<template>
  <div>
    <a-modal 
      v-model="visible" 
      :confirmLoading="loading" 
      :maskClosable="false" 
      @cancel="cancel" 
      @ok="confirm" 
      :width="1200"
    >
      <div slot="title">入库商品选择</div>

      <div class="modal-body">
        <!-- 产品列表 -->
        <a-row v-for="(order) in orders" :key="order.id" style="margin-bottom: 12px; display: flex; align-items: center;">
          <!-- 勾选框 -->
          <div style="margin-right: 20px;">
            <span class="label">产品sku:</span>
            <span class="value" style="margin-right: 20px;">{{ order.system_product.system_sku }}</span>
          </div>
          
          <!-- 图片 -->
          <img :src="order.system_product.primary_image" style="width: 50px; height: 50px; margin-right: 20px;" alt="产品主图" />
          
          <!-- 其他产品信息 -->
          <div style="margin-right: 20px;">
            <span class="label">打包备注:</span>
            <span class="value">{{ order.system_product.purchase_mark }}</span>
          </div>

          <!-- 数量输入框 -->
          <div style="margin-right: 20px;">
            <span class="label">到货数量输入:</span>
            <a-input-number 
              v-model="order.input_quantity" 
              :min="0" 
              style="margin-left: 20px;" 
              placeholder="输入数量" 
            />
          </div>

          <!-- 打包异常留言 -->
          <div>
            <span class="label">异常留言:</span>
            <a-input 
              v-model="order.msg" 
              style="margin-left: 20px; width: 250px;" 
              placeholder=" 异常留言" 
            />
          </div>

        </a-row>
      </div>
    </a-modal>
  </div>
</template>

<script>
import { signTheSystemProductsOfPurchaseOrder } from '@/api/packer'
import { message } from "ant-design-vue";

export default {
  name: 'FormModal',
  props: ['visible', 'form', 'posting_number'],
  model: { prop: 'visible', event: 'cancel' },
  data() {
    return {
      loading: false,
      orders:[],
      iframe: false
    };
  },
  computed: {
  },
  watch: {
    form: {
      handler(newValue) {
        this.orders = this.form?this.form.map(order => ({
          ...order,
          input_quantity: 0,
          msg:""
        })):[]
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    confirm() {
      this.loading = true;

      const selectedProducts = this.orders
        .map(order => ({
          id: order.id,
          quantity: order.input_quantity,
          msg:order.msg
        }));
      
      signTheSystemProductsOfPurchaseOrder({purchase_order_msgs:selectedProducts,posting_number:this.posting_number}).then(
        res => {
          if (res.data){
            // 触发打印
            const pdfBase64 = res.data;
            const pdfBlob = base64ToBlob(pdfBase64);
            const pdfUrl = URL.createObjectURL(pdfBlob);

            if(this.iframe == false){
              this.iframe = document.createElement('iframe');
              document.body.appendChild(this.iframe);
            }

            let iframe = this.iframe

            iframe.style.display = 'none'; // 隐藏 iframe
            iframe.src = pdfUrl;
            

            iframe.onload = () => {
              iframe.contentWindow.print(); // 触发打印
              message.success(res.msg);
              this.$emit('update');
            };

            // 将 base64 编码的 PDF 转换为 Blob
            function base64ToBlob(base64) {
              const byteCharacters = atob(base64);
              const byteArrays = [];

              for (let offset = 0; offset < byteCharacters.length; offset += 1024) {
                const slice = byteCharacters.slice(offset, offset + 1024);
                const byteNumbers = new Array(slice.length);
                for (let i = 0; i < slice.length; i++) {
                  byteNumbers[i] = slice.charCodeAt(i);
                }
                byteArrays.push(new Uint8Array(byteNumbers));
              }

              return new Blob(byteArrays, { type: 'application/pdf' });
            }
          }else{
            message.success(res.msg);
            this.$emit('update');
          }
          
        }
      ).finally(() => {
        this.loading = false;
      })
    },
    cancel() {
      this.$emit('update');
    },
  },
}
</script>

<style scoped>
.modal-body { 
  max-height: 60vh;
  overflow-y: auto; 
}

.row {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.row .columns {
  display: flex;
  margin-right: 30px;
  align-items: center
}

.label{
  width: 100px;
  margin-right: 5px;;
}

.value{
  margin-right: 5px;
  color: green;
}
</style>